import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import styles from "../screens/styles.module.css";
import Loading from "./UI/Loading";
import axios from "axios";

const DisplayPhoto = ({ user, history }) => {
  const dispatch = useDispatch();
  //   const ainfo = useSelector((state) => state.ainfo);
  //   const { loading, error, success } = ainfo;

  useEffect(() => {
    if (!user) {
      history.push("/profile");
    }
  }, [user, history]);

  const [image, setImage] = useState();
  const [uploading, setUploading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);
  const changeHandler = (event) => {
    setImage(event.target.files[0]);
    console.log(image);
  };

  const uploadFileHandler = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("files", image);
    setUploading(true);
    const userInfo = JSON.parse(localStorage.getItem("userInfo"));
    const us = "12345";
    try {
      const config = {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      const { data } = await axios.post("/api/v1/staff/upload", { us }, config);

      //setImage(data);
      setUploading(false);
      setSuccess(true);
    } catch (error) {
      console.error(error);
      setUploading(false);
      setError(true);
    }
  };
  return (
    <div className={styles.forms}>
      {error && <div className="alert alert-info">{error}</div>}
      {success && <div className="alert alert-success">Photo Uploaded</div>}
      {uploading ? (
        <Loading />
      ) : (
        <>
          <form onSubmit={uploadFileHandler} encType="multipart/form-data">
            <div className={`row`}>
              <h2 className="alert alert-danger">
                Pictures cannot be uploaded now
              </h2>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Display Picture</label>
                <input type="file" onChange={changeHandler} />
              </div>

              <div className={`col-md-12 ${styles.formPadding}`}>
                <label />
                <input
                  type="submit"
                  className="btn btn-dark form-control"
                  value="Save"
                  onClick={uploadFileHandler}
                />
              </div>
            </div>
          </form>
        </>
      )}
    </div>
  );
};

export default DisplayPhoto;
