import React from "react";
import { Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import styles from "./styles.module.css";

const Steps = () => {
  return (
    <div className={styles.steps}>
      <Row>
        <Col>
          <Link to="/edit/profile/">Basic Information</Link>
        </Col>
        <Col>
          <Link to="/edit/profile/emergency-info">Emergency Contact</Link>
        </Col>
        <Col>
          <Link to="/edit/profile/office-info">Official Information</Link>
        </Col>
        <Col>
          <Link to="/edit/profile/account-info">Account Information</Link>
        </Col>
        <Col>
          <Link to="/edit/profile/photo-upload">Photo Upload</Link>
        </Col>
      </Row>
    </div>
  );
};

export default Steps;
